import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import About from './views/About.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/home',
      component: () => import('./views/Home'),
      children: [
        {
          path: 'qrcode',
          component: () => import('./components/home/QRcode')
        },
        {
          path: 'shuttle',
          component: () => import('./components/home/shuttle')
        },
        {
          path: 'shuttleDetails',
          component: () => import('./components/home/shuttleDetails')
        },
        {
          path: 'map',
          component: () => import('./components/home/map')
        }
      ]
    },
    {
      path: '/about',
      name: 'about',
      component: About
    }
  ]
})
